body {
  transition: 0.5s;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.tre {
  width: 40vh !important;
  height: 40vh !important;
}

.loadert {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 100;
}
.animation {
  animation-name: opacity;
  animation-delay: 1s;
  animation-duration: 1s;
}

@keyframes opacity {
  from {
    opacity: 1;
    width: 30vh !important;
    height: 30vh !important;
  }
  to {
    opacity:0;
    width: 1px !important;
    height: 1px !important;
    display: none;
  }
}

@keyframes ytica {
  from {
    flex:0;
  }
  to {
    flex: 0 50%;
  }
}

@keyframes yticapo {
  to {
    flex:0;
  }
  from {
    flex: 0 50%;
  }
}

#filter-button button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

#filter-button span:hover {
  border-bottom: 2px solid #b5f5ea;
}

.plusplus {
  position: absolute; 
  left: 0px; top: 0px;
}

.no-show, .no-show img, .no-show figure, .no-show figcaption{
  transition: 0.5s;
  animation-name: yticapo;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  padding: 0 0 !important;
  margin: 0 !important;
}


.show {
  padding: 0 2px !important;
  margin: 0 !important;
  transition: 0.5s;
  transition: 0.5s;
  animation-name: ytica;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.no {
  display: none
}

.feature {
  width: 100vw;
  height: 30vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.top-border {
  border-top: solid 10px #009688;
}

#project-gallery {
  min-height: 500px;
}

@media only screen and (min-width: 600px) {
  .login-container {
    width: 50vw;
    height: 50vh;
    margin: 25vh auto;
  }
  .feature {
    position: absolute;
    right: 5vw;
    top: 5vw;
    width: 50vw;
    height: 80vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0 0 24px -20px;
    border-radius: 4px;
    z-index: 0;
  }
  
  .hund {
    height: 100vh;
  }

  @keyframes ytica {
    from {
      flex:0;
    }
    to {
      flex: 0 0 33%;
    }
  }
  
  @keyframes yticapo {
    to {
      flex:0;
    }
    from {
      flex: 0 0 33%;
    }
  }

  .no-show, .no-show img, .no-show figure, .no-show figcaption{
    padding: 0 0 !important;
    margin: 0 !important;
  }
  
  
  .show {
    padding: 0 10px !important;
    margin: 0 !important;
  }
}
